        // 实现地球大气层效果
        const atmosphereSphereGeometry = new THREE.SphereGeometry(121, 128, 128)
        const atmosphereMaterial = new THREE.ShaderMaterial({
            uniforms: {},
            vertexShader: `
            varying vec3 vNormal; 
            
            void main() { 
                vNormal = normalize( normalMatrix * normal ); 
                gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 ); 
            }
        `,
            fragmentShader: `
            varying vec3 vNormal; 
    
            void main() { 
                float intensity = pow( 0.7 - dot( vNormal, vec3( 0.0, 0.0, 0.5 ) ), 3.0 ); 
                gl_FragColor = vec4( 10, 10, 10, 3.0 ) * intensity; 
            }
        `,
            blending: THREE.AdditiveBlending,
            transparent: true,
            // depthTest: false,
            // depthWrite: false,
        })
        const atmosphereSphere = new THREE.Mesh(atmosphereSphereGeometry, atmosphereMaterial)
