        // 创建人物标签
        const css3DRenderer = new CSS3DRenderer()
        css3DRenderer.setSize(window.innerWidth, window.innerHeight)
        css3DRenderer.domElement.style.position = 'absolute'
        css3DRenderer.domElement.style.pointerEvents = 'none'
        document.querySelector(".container").appendChild(css3DRenderer.domElement)

        const persons = []
        const personGroup = new THREE.Group()
        scene.add(personGroup)

        const R = 150
        // 加载人物模型
        function loadPersonModel(modelUrls) {
            const loader = new FBXLoader(manager)
            for (let i = 0; i < modelUrls.length; i++) {
                loader.load(modelUrls[i], fbx => {
                    const personObject = new THREE.Object3D()
                    personObject.index = i
                    const x = R * Math.sin(i * Math.PI / 6)
                    const z = R * Math.cos(i * Math.PI / 6)
                    fbx.position.set(x, -100, z)
                    fbx.rotation.y = i * Math.PI / 6
                    fbx.scale.setScalar(.5)
                    persons.push(fbx)
                    personObject.add(fbx)

                    // 创建人物标签
                    const label = document.createElement('div')
                    label.className = 'label'
                    label.innerHTML = mainBodyNames[i] // 标签内容
                    const css3DObject = new CSS3DObject(label)
                    personInfo.currentIndex === i ? css3DObject.visible = true : css3DObject.visible = false
                    css3DObject.scale.setScalar(0.27)
                    css3DObject.position.set(fbx.position.x, fbx.position.y + 45, fbx.position.z)
                    css3DObject.rotation.y = i * Math.PI / 6
                    personObject.add(css3DObject)

                    const sprite = new THREE.Sprite(
                        new THREE.SpriteMaterial({
                            map: new THREE.TextureLoader(manager).setPath("./public/images/").load('circle_selected.png'),
                            transparent: true,
                        })
                    )
                    sprite.position.set(x, -100, z)
                    sprite.scale.setScalar(10)
                    personObject.add(sprite)
                    personGroup.add(personObject)
                })
            }
            // console.log(personGroup);
        }

        loadPersonModel(testModelUrls)

        //'rgba(25,75,131,1)'
        function paintRing({ innerRadius, outerRadius, thetaSegments, color, opacity, position }) {
            const ringMesh = new THREE.Mesh(
                new THREE.RingGeometry(innerRadius, outerRadius, thetaSegments),
                new THREE.MeshBasicMaterial({
                    color: new THREE.Color(color),
                    transparent: true,
                    side: THREE.DoubleSide,
                    opacity
                })
            )
            ringMesh.rotation.x = -Math.PI / 2
            ringMesh.position.set(position.x, position.y, position.z)
            personGroup.add(ringMesh)
        }

