        function paintRing2() {
            const planeGeometry = new THREE.PlaneGeometry(R * 2, R * 2, 64, 64)
            const planeMaterial = new THREE.MeshBasicMaterial({
                map: new THREE.TextureLoader(manager).load('./public/images/ring.png'),
                transparent: true,
                depthWrite: false,
                side: THREE.DoubleSide
            })
            const plane = new THREE.Mesh(planeGeometry, planeMaterial)
            plane.rotation.x = -Math.PI / 2
            plane.position.set(0, -100, 0)
            console.log(plane);
            personGroup.add(plane)
        }

