        const raycaster = new THREE.Raycaster()
        const pointer = new THREE.Vector2()

        const floatWinDiv = document.querySelector('.float-window-container')
        const infoWinDiv = document.querySelector('.info-window-container')

        const object = new THREE.Object3D()
        window.addEventListener('mousemove', event => {
            pointer.x = (event.clientX / window.innerWidth) * 2 - 1
            pointer.y = -(event.clientY / window.innerHeight) * 2 + 1
            const intersects = dotRaycaster.intersectObjects(dots)
            if (intersects.length > 0) {
                const dot = intersects[0].object
                // 通过欧拉角获取旋转之后的坐标
                const v3 = dot.position.clone().applyEuler(earthGroup.rotation)
                object.position.set(v3.x, v3.y, v3.z)
                renderer.domElement.style.cursor = 'pointer'
                const project = data.data[personInfo.currentIndex].projects.find(project => project.projectId == dot.projectId)
                console.log(project, dot.projectId);
                project && (floatWinDiv.innerHTML = `${project.projectName}<br/>位置：${project.location}`)
                floatWindow(floatWinDiv, object, true)
            } else {
                renderer.domElement.style.cursor = 'default'
                floatWindow(floatWinDiv, null, false)
            }
        })

        

        const infoUls = document.querySelectorAll('.container .content>ul')
        infoUls.forEach((infoUl, ulIndex) => {
            infoUl.onmouseover = function (event) {
                let target = event.target
                const is = [...infoUl.querySelectorAll('li>i')]
                const lis = [...infoUl.querySelectorAll('li')]
                let index 
                if(is.indexOf(target) === -1 && lis.indexOf(target) === -1) {
                    index = -1
                }else if(is.indexOf(target) === -1) {
                    index = lis.indexOf(target)
                }else {
                    index = is.indexOf(target) 
                }
                if (target.nodeName !== 'UL' || target.nodeName === 'LI' || index !== -1) {
                    if (index !== -1) {
                        target = lis[index]
                        index = lis.indexOf(target)
                    }
                    const rect = target.getBoundingClientRect()
                    const { width, height } = getElWidthAndHeight(target)
                    infoWinDiv.style.left = (rect.left - width / 10) + 'px'
                    infoWinDiv.style.top = (rect.top - height - 5) + 'px'
                    infoWinDiv.style.display = 'block'
                    const attribute = data.data[personInfo.currentIndex]?.categories[ulIndex]?.attributeItem[index]?.attribute
                    if (attribute !== undefined) {
                        infoWinDiv.innerHTML = attribute
                    }
                }
            }

            infoUl.onmouseout = function () {
                infoWinDiv.style.display = 'none'
            }
        })


