        window.addEventListener('mousemove', event => {
            const intersects = raycaster.intersectObjects(persons)
            if (intersects.length > 0) {
                renderer.domElement.style.cursor = 'pointer'
            } else {
                renderer.domElement.style.cursor = 'default'
            }
        })

        window.addEventListener('click', event => {
            setTimeout(() => {
                const intersects = raycaster.intersectObjects(persons)
                // console.log(intersects);
                if (intersects.length > 0) {
                    const intersect = intersects[0].object;
                    const index = intersect.parent.parent.index;
                    if (personInfo.currentIndex == index) {
                        return;
                    }
                    const angle = (personInfo.currentIndex - index) * Math.PI / 6
                    // console.log(`点击了第${index}个人物，需要旋转${angle}弧度`);
                    gsap.to(personGroup.rotation, {
                        y: personGroup.rotation.y + angle,
                        duration: 1,
                        ease: 'power2.out',
                        onComplete: () => {
                            showOrHidePersonLabel()
                        }
                    })
                    personInfo.previousIndex = personInfo.currentIndex
                    personInfo.currentIndex = index
                    console.log(`当前人物索引：${personInfo.currentIndex},上一个人物索引：${personInfo.previousIndex}`);
                    createRemarkFlyLine(data.data[personInfo.currentIndex].projects)
                    modifyCategoryInfo(data.data[personInfo.currentIndex].categories)
                }
            }, 50)
        })

        function floatWindow(el, object, isShow) {
            if (isShow) {
                const worldPos = getScreenPosition(object.position)
                const { width, height } = getElWidthAndHeight(el)
                el.style.left = (worldPos.x) + 'px'
                el.style.top = (worldPos.y - height * 1.5) + 'px'
                el.style.display = 'block'
            } else {
                el.style.display = 'none'
            }
        }

        /**
         * @description 显示或隐藏人物的头顶标签
         * */
        function showOrHidePersonLabel() {
            try {
                personGroup.children.forEach(child => {
                    if (!child.isMesh && (child.index === personInfo.currentIndex || child.index === personInfo.previousIndex)) {
                        child.children.forEach(childItem => {
                            if (childItem instanceof CSS3DObject) {
                                if (child.index === personInfo.currentIndex) {
                                    childItem.visible = true
                                } else if (child.index === personInfo.previousIndex) {
                                    childItem.visible = false
                                }
                            }
                        })
                    }
                })
            } catch (err) {

            }
        }

        /** 
         * @description 获取元素的宽高
         * @param {HTMLElement} el
         * @returns {Object} {width, height}
        */
        function getElWidthAndHeight(el) {
            let style = window.getComputedStyle(el)
            let width = style.width.replace('px', '')
            let height = style.height.replace('px', '')
            width = parseFloat(width)
            height = parseFloat(height)
            return {
                width,
                height
            }
        }

        const loadDiv = document.createElement('div')
        loadDiv.style.position = 'absolute'
        loadDiv.style.top = '50%'
        loadDiv.style.left = '50%'
        loadDiv.style.transform = 'translate(-50%, -50%)'
        loadDiv.style.fontSize = '20px'
        loadDiv.style.color = '#fff'
        loadDiv.innerHTML = '加载中...'
        document.body.appendChild(loadDiv)

        let intersect
        manager.onProgress = (event, loaded, total) => {
            loadDiv.innerHTML = `加载中...${Math.floor(loaded / total * 100)}%`
            if (loaded >= total) {
                loadDiv.style.display = 'none'
                intersect = raycaster.intersectObject(earthGroup)
                data.data[0] && categoryEl.forEach(category => {
                    category.style.opacity = 1
                })
                data.data[0]&&modifyCategoryInfo(data.data[0].categories)
            }
        }

        const controls = new OrbitControls(camera, renderer.domElement)
        controls.enabled = false
        controls.enableDamping = true

        const axesHelp = new THREE.AxesHelper(300)
        // scene.add(axesHelp)

        let ratio = {
            value: 0
        }

        const render = () => {
            ratio.value += 0.01
            controls.update()
            renderer.render(scene, camera)
            css3DRenderer.render(scene, camera)
            raycaster.setFromCamera(pointer, camera)
            dotRaycaster.setFromCamera(pointer, camera)
            requestAnimationFrame(render)
            // console.log(camera.position);
        }
        render()

        document.querySelector(".arrow-left").onclick = function () {
            personInfo.previousIndex = personInfo.currentIndex
            if (++personInfo.currentIndex >= testModelUrls.length) {
                personInfo.currentIndex = 0
            }
            gsap.to(personGroup.rotation, {
                duration: .8,
                y: -Math.PI / 6 * personInfo.currentIndex,
                ease: 'linear',
                onComplete: () => {
                    showOrHidePersonLabel()
                }
            })
            data.data[0]&&createRemarkFlyLine(data.data[personInfo.currentIndex].projects)
            data.data[0]&&modifyCategoryInfo(data.data[personInfo.currentIndex].categories)
        }

        document.querySelector(".arrow-right").onclick = function () {
            personInfo.previousIndex = personInfo.currentIndex
            if (--personInfo.currentIndex < 0) {
                personInfo.currentIndex = testModelUrls.length - 1
            }
            console.log(personInfo.currentIndex);
            gsap.to(personGroup.rotation, {
                duration: .8,
                y: -Math.PI / 6 * personInfo.currentIndex,
                ease: 'linear',
                onComplete: () => {
                    showOrHidePersonLabel()
                }
            })
            data.data[0]&&createRemarkFlyLine(data.data[personInfo.currentIndex].projects)
            data.data[0]&&modifyCategoryInfo(data.data[personInfo.currentIndex].categories)
        }

        window.addEventListener('resize', () => {
            camera.aspect = window.innerWidth / window.innerHeight
            camera.updateProjectionMatrix()
            renderer.setSize(window.innerWidth, window.innerHeight)
            renderer.setPixelRatio(window.devicePixelRatio)
            css3DRenderer.setSize(window.innerWidth, window.innerHeight)

            changeDialogPos()
        })

        /** 
         * @description: 将世界坐标转换为屏幕坐标
         * @param vector3 THREE.Vector3 世界坐标
         * @return {x, y} 屏幕坐标
        */
        function getScreenPosition(vector3) {
            const v3 = new THREE.Vector3(vector3.x, vector3.y, vector3.z).project(camera)
            return {
                x: Math.round(v3.x * window.innerWidth / 2 + window.innerWidth / 2),
                y: Math.round(-v3.y * window.innerHeight / 2 + window.innerHeight / 2)
            }
        }

        let earthR = 0 // 地球模型在屏幕坐标中的半径
        function changeDialogPos() {
            if (radius) {
                const earthPosition = new THREE.Vector3(-radius, 0, 0).clone().project(camera)
                const x = earthPosition.x * window.innerWidth / 2 + window.innerWidth / 2
                const y = -earthPosition.y * window.innerHeight / 2 + window.innerHeight / 2
                earthR = window.innerWidth / 2 - x

                const tan0 = document.getElementById('tan_0')
                tan0.style.top = `${y - earthR - 10}px`
                tan0.style.transform = 'translate(-50%, -120%)'

                const tan2 = document.getElementById('tan_2')
                tan2.style.left = (x - 8) + 'px'
                tan2.style.top = y + 'px'
                tan2.style.transform = 'translate(-140%,-50%)'

                const tan4 = document.getElementById('tan_4')
                tan4.style.right = (x - 10) + 'px'
                tan4.style.top = y + 'px'
                tan4.style.transform = 'translate(140%,-50%)'

                const tan1 = document.getElementById('tan_1')
                tan1.style.left = (window.innerWidth / 2 - earthR * Math.cos(Math.PI / 4) - 10) + 'px'
                tan1.style.top = (window.innerHeight / 2 - earthR * Math.sin(Math.PI / 4)) + 'px'
                tan1.style.transform = 'translate(-135%,-100%)'

                const tan3 = document.getElementById("tan_3")
                tan3.style.right = (window.innerWidth / 2 - earthR * Math.cos(Math.PI / 4) - 10) + 'px'
                tan3.style.top = (window.innerHeight / 2 - earthR * Math.sin(Math.PI / 4)) + 'px'
                tan3.style.transform = 'translate(135%,-100%)'
            }
        }

        // 控制地球旋转
        let isDown = false // 表示鼠标是否处于按下状态
        let isEnableMove = false // 表示是否可以移动

        let oldPos = {
            x: 0,
            y: 0
        }
        let angle = {
            xAngle: 0.64 * 180 / Math.PI,
            yAngle: 2.73 * 180 / Math.PI
        }

        window.addEventListener('mousedown', event => {
            isDown = true
            oldPos.x = event.clientX
            oldPos.y = event.clientY
            // 鼠标按下的范围在地球模型上
            const bevelEdge = Math.pow((window.innerWidth / 2 - event.clientX), 2) + Math.pow((window.innerHeight / 2 - event.clientY), 2)
            bevelEdge <= Math.pow(earthR + 15, 2) ? isEnableMove = true : isEnableMove = false
            window.addEventListener('mousemove', mouseMove)
        })

        window.addEventListener('mouseup', () => {
            isDown = false
            oldPos.x = 0
            oldPos.y = 0
            document.removeEventListener('mousemove', mouseMove)
        })

        function mouseMove(event) {
            if (isDown && earthGroup && isEnableMove) {
                const newPos = {
                    x: event.clientX,
                    y: event.clientY
                }
                const dx = newPos.x - oldPos.x
                const dy = newPos.y - oldPos.y
                angle.xAngle += dy * 0.2
                angle.yAngle += dx * 0.2
                earthGroup.rotation.x = angle.xAngle * Math.PI / 180
                earthGroup.rotation.y = angle.yAngle * Math.PI / 180
                oldPos.x = newPos.x
                oldPos.y = newPos.y
            }
        }

        // 经纬度转球面坐标
        function getShperePosition(R, longitude, latitude) {
            let long = longitude * Math.PI / 180
            const lat = latitude * Math.PI / 180
            long = -long
        
            const x = R * Math.cos(lat) * Math.cos(long)
            const y = R * Math.sin(lat)
            const z = R * Math.cos(lat) * Math.sin(long)
            return new THREE.Vector3(x, y, z)

            /* var lg = THREE.MathUtils.degToRad(longitude)
            var lt = THREE.MathUtils.degToRad(latitude)
            var temp = R * Math.cos(lt)
            var x = temp * Math.sin(lg)
            var y = R * Math.sin(lt)
            var z = temp * Math.cos(lg)
            return new THREE.Vector3(x, y, z) */

            // const theta = (90 + longitude) * (Math.PI / 180)
            // const phi = (90 - latitude) * (Math.PI / 180)
            // return (new THREE.Vector3()).setFromSpherical(new THREE.Spherical(R, phi, theta))
        }

        // 在球面上创建定位点
        const dotGroup = new THREE.Group()
        earthGroup.add(dotGroup)
        function createPositionDot(vec3, projectId) {
            const dot = new THREE.Mesh(
                new THREE.PlaneGeometry(10, 10),
                new THREE.MeshBasicMaterial({
                    color: '#1affff',
                    map: new THREE.TextureLoader().load("./public/images/1-1.png"),
                    // side: THREE.DoubleSide,
                    transparent: true,
                    // depthTest: false,
                    depthWrite: false,
                    // blending: THREE.AdditiveBlending
                })
            )
            dot.name = 'dot'
            dot.projectId = projectId
            dot.position.set(vec3.x, vec3.y, vec3.z)
            dot.quaternion.setFromUnitVectors(
                new THREE.Vector3(0, 0, 1),
                vec3.clone().normalize()
            )
            dot.rotation.z = Math.PI + .3
            dots.push(dot)
            gsap.to(dot.scale, {
                x: 1.1,
                y: 1.1,
                duration: Math.random() + .5,
                repeat: -1,
                yoyo: true,
            })
            dotGroup.add(dot)
        }

        function removePositionDot() {
            dots.length = 0
            earthGroup.traverse(child => {
                if ((child.isMesh || child.isPoints) && child.name === 'dot') {
                    child.visible = false
                }
            })
        }

        /** 
         * startVector: 起点坐标
         * endVector: 终点坐标
         * height: 飞线高度
         * size: 点的尺寸大小
         * color: 点的颜色
         * range: 飞线的长度
         * speed: 飞线的速度
        */
        function startFlyLine(options) {
            const { startVector, endVector, height, size, color, range, speed } = options
            const _start = new THREE.Vector3(startVector.x, startVector.y, startVector.z)
            const _end = new THREE.Vector3(endVector.x, endVector.y, endVector.z)

            const _center = _start.clone().lerp(_end, 0.5)
            _center.y += height
            // 计算出飞线点的数量
            const number = parseInt(_start.distanceTo(_center) + _end.distanceTo(_center))
            // 创建贝塞尔曲线
            const curve = new THREE.QuadraticBezierCurve3(_start, _center, _end)
            // 创建飞线点
            const points = curve.getPoints(number)
            // 保存所有飞线点坐标
            const positions = []
            const current = []

            points.forEach((item, index) => {
                positions.push(item.x, item.y, item.z)
                current.push(index)
            })

            const geometry = new THREE.BufferGeometry()
            const material = new THREE.ShaderMaterial({
                transparent: true,
                blending: THREE.AdditiveBlending,
                uniforms: {
                    uSize: { // 点的大小
                        value: size
                    },
                    uTime: ratio, // 时间
                    uColor: { // 颜色
                        value: new THREE.Color(color)
                    },
                    uRange: { // 飞线长度
                        value: range
                    },
                    uTotal: { // 轨迹总长度，（点的总个数）
                        value: number
                    },
                    uSpeed: { // 飞行速度
                        value: speed
                    }
                },
                vertexShader: customShader.vertexShader,
                fragmentShader: customShader.fragmentShader
            })
            geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3))
            geometry.setAttribute('current', new THREE.Float32BufferAttribute(current, 1))
            const flyPoints = new THREE.Points(geometry, material)
            flyPoints.name = 'dot'
            dotGroup.add(flyPoints)
        }
        const arcLineGroup = new THREE.Group()
        earthGroup.add(arcLineGroup)

        const frameIdArr = []
        const flyArcArr = []


